<template>
  <div >
    <section id="header">
      <HeaderPublc></HeaderPublc>
    </section>
    <section id="cent">
      <el-dialog  :visible.sync="dialogVisible" width="430px" >
        <el-input  v-model="fromData.recyclePosition.address"  placeholder="请输入回收地址"></el-input>
        <span slot="footer" class="dialog-footer" style="text-align: center">
          <el-button type="primary" size="medium" round @click="updateStatusPost()" style="width: 120px">确 定</el-button>
          <el-button size="medium" round v-on:click="updatStatusFn(listData.id)"  style="width: 120px">取 消</el-button>
          </span>
      </el-dialog>
      <div class="top">
      </div>
      <div class="center">
        <div class="cardBox clearfix" :DetailData="DetailData"  v-loading="loading" :element-loading-text="languageData.public.loadingText" element-loading-spinner="el-icon-loading" element-loading-background="#f7f7f7">
          <div class="goBack" @click="goBack()"><p class="el-icon-arrow-left"><span style="cursor: pointer">返回</span></p></div>
          <div class="card clearfix"   v-on:click="goChart(listData)">
            <div class="span6 device  clearfix">
              <div class=" textBox address clearfix">
                <div class="left ctiy">{{languageData.id=='Chinese'?listData.startPosition.country.CN:listData.startPosition.country.EN}}</div>
                <div class="left iconBox">
                  <div class="iocn">
                    <img :src="listData.icon">
                  </div>
                  <div class="l"></div>
                  <p class="go">{{languageData.id=='Chinese'?listData.transportStatusCn:listData.transportStatusEn}}</p>
                </div>
                <div class="left ctiy">{{languageData.id=='Chinese'?listData.endPosition.country.CN:listData.endPosition.country.EN}}</div>
                <div class="cleae"></div>
                <div class="text" >
                  <p style="cursor: pointer" @click.stop="gomap(listData)"><span class="pt"> <i class="el-icon-location"></i>{{languageData.header.transport}}：{{listData.now}}<i class="el-icon-arrow-right"></i></span> </p>
                </div>
              </div>
            </div>
            <div class="span6 device   clearfix">
              <div class="textBox deviceT">
                <!--柜号-->
                <p style="position: relative"><label>{{languageData.public.plalte}}：</label><span>{{listData.containerId}}</span><span class="copy" :data-clipboard-text="listData.containerId" v-on:click="copyFn()"> {{languageData.public.copy}} </span></p>
                <!--提单号-->
                <p style="position: relative"><label>{{languageData.public.ladingIdBill}}：</label><span v-show="!ladingIdBillShow">{{listData.ladingId}}</span>
                  <input type="text"  v-show="ladingIdBillShow && listData.transportStatus!=1" class="ladingIdBill" v-model="listData.ladingId"/>
                  <span class="update" v-show="!ladingIdBillShow && listData.transportStatus!=1" @click="updateBill()">修改</span>
                  <span class="update" v-show="ladingIdBillShow && listData.transportStatus!=1" @click="updateBill(listData.id)">确定</span>
                </p>
                <!--设备号-->
                <p><label>{{languageData.public.container}}：</label><span>{{listData.deviceId}}</span></p>
              </div>
            </div>
            <div class="span6 device  clearfix">
              <div class="deviceStatus textBox deviceT">
                <!--交通工具-->
                <p><label>{{languageData.public.modeOfTransport}}： </label><span> {{languageData.id=='Chinese'?listData.transportTypeText:listData.transportTypeTextEn}} </span></p>
                <!--运输状态-->
                <p><label>{{languageData.public.status}}： </label><span> {{languageData.id=='Chinese'?listData.transportStatusCn:listData.transportStatusEn}}  </span></p>
                <!--设备状态-->
                <p><label>{{languageData.public.deviceStatus}}： </label><span> {{languageData.id=='Chinese'?listData.deviceOnfftext.CN:listData.deviceOnfftext.EN}}  </span></p>
                <!--创建时间-->
                <p><label>{{languageData.public.creaedTime}}： </label><span> {{listData.ymd}} </span></p>
                <p v-show="listData.completeTime != 'null null'"><label>完成时间： </label><span> {{listData.completeTime}} </span></p>
                <!--定位频率-->
                <p> <label>定位频率： </label><span>{{listData.locateFrequency}}&nbsp{{languageData.id=='Chinese'?listData.frequencyTypeText.CN:listData.frequencyTypeText.EN}}</span></p>
                <!--起始地-->
                <p> <label>起始地： </label><span >{{languageData.id=='Chinese'?listData.startPosition.address.CN:listData.startPosition.address.EN}}</span></p>
                <!--目的地-->
                <p> <label>目的地： </label><span>{{languageData.id=='Chinese'?listData.endPosition.address.CN:listData.endPosition.address.EN}}</span></p>
              </div>
            </div>
            <div style="text-align: right;padding-top: 40px;">
              <el-button round class="bot" :updateStatusData="updateStatusData" v-on:click="del(listData.id)">{{languageData.public.Delete}}</el-button>
              <el-button round class="okBot" v-show="listData.transportStatus!=1" v-on:click="dialogVisible = true">{{languageData.public.completion}}</el-button>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>
<style scoped>
  #cent{background-color: #f7f7f7}
  .cardBox{width:70%;min-width: 980px;max-width:1500px;padding: 40px;margin: auto;position: relative;}
  .card{min-height: 140px;width:100%;margin-top:20px;background-color: #fff;position: relative;padding: 40px;}
  .device { }
  .goBack{ text-align: left}
  .device p{ text-align: left;line-height: 34px;}
  .deviceStatus p{font-size: 12px;line-height: 28px;}
  .textBox{padding: 15px;margin: auto;position: relative}
  .deviceT{width:90%; }
  .address{ padding-left:30px;max-width:600px;min-width: 280px;margin-left: 0px;position: relative}
  .go{font-size: 12px;text-align: right ;text-indent:50px;}
  .ctiy{font-size: 28px;font-weight: bold;}
  .iconBox{width:100px;line-height: 40px;border-bottom: 2px solid #e1e1e1;position: relative;padding-right: 5px;padding-left: 5px;margin: 0px 15px;top: -15px;}
  .iocn{width: 25px;height:25px;background: #e1e1e1;border-radius:16px;  position: absolute;left: 20px;top:5px;}
  .iocn img { width: 100%;height: 100%}
  .l{width: 10px;height: 1px;border-bottom: 2px solid #e1e1e1;transform:rotate(45deg);
    -ms-transform:rotate(45deg);
    -moz-transform:rotate(45deg);
    -webkit-transform:rotate(45deg);position: absolute;right: -2px;bottom:1px;}
  #cent input{ border: 0px none}
  .address .text{padding-top:40px;width: auto;max-width: 600px;}
  .address .text .pt{padding-left:20px;padding-top: 5px;padding-bottom: 5px;padding-right: 5px; background: #e1f3ff;font-size: 14px;line-height: 28px;height: 28px;width: auto}
  .timeDate p{text-align: left;line-height: 40px;font-size: 18px;}
  #cent .timeDate .data{font-size: 18px;}
  .bot{width: 104px;height: 40px;margin-bottom: 20px;border: 1px solid #e5e5e5;background-color:#e5e5e5 }
  .bot:hover{background-color: #409eff;border-color: #409eff;color: #fff}
  .okBot{background-color: #0e72b9; border-color: #0e72b9 ;color: #fff}
  .okBot:hover{background-color: #409eff;border-color: #409eff}
  .copy{padding: 5px 5px;margin-left: 40px; width: 40px;height: 20px;border: 1px solid #ccc; text-align: center;font-size: 12px;line-height: 20px;cursor: pointer}
  .update{color:#409eff;margin-left: 40px; text-align: center;font-size: 12px;line-height: 20px;cursor: pointer}
  #cent .ladingIdBill{border:1px solid #409eff;width: 150px;height: 25px;padding-left: 5px;}
</style>
<script>
  import HeaderPublc from '../public/herder.vue'
  import Clipboard from 'clipboard'
  import Public from '../../vuex/public.js'
  import jsonListData from '../../../static/local.json'
  import {mapState, mapActions} from 'vuex'
  import ElButton from "../../../node_modules/element-ui/packages/button/src/button.vue";
  import IconCarNor from '../../../static/img/icon/icon_car@2x.png'
  import IconCarSel from '../../../static/img/icon/icon_car_sel.png'
  import IconPlantNor from '../../../static/img/icon/icon_plane@2x.png'
  import IconPlantSel from '../../../static/img/icon/icon_plant_sel.png'
  import IconShipNor from '../../../static/img/icon/icon_ship@2x.png'
  import IconShipSel from '../../../static/img/icon/icon_ship_sel.png'
  import IconTrainNor from '../../../static/img/icon/icon_train@2x.png'
  import IconTrainSel from '../../../static/img/icon/icon_train_sel.png'
  export default {
    name: 'my-ChartSon',
    data () {
      return {
        id: null,
        year: null,
        //
        list: [
          {
            id: 'AKDFAKJ2', // 柜号/车牌号
            startPosition: { // 起始地
              country: 'china',
              city: 'Beijing',
              address: 'XXXXX'
            },
            endPosition: { // 目的地
              country: 'china',
              city: 'Beijing',
              address: 'XXXXX'
            },
            transportStatus: 1, // 运输状态
            transportType: 2 // 运输类型
          },
          {
            id: 'AKDFAKJ2',
            startPosition: {
              country: 'china',
              city: 'Beijing',
              address: 'XXXXX'
            },
            endPosition: {
              country: 'china',
              city: 'Beijing',
              address: 'XXXXX'
            },
            transportStatus: 1,
            transportType: 2
          }
        ],
        // 渲染列表
        listData: {
          id: 1,  // 后台唯一id
          containerId: 'QWE1254', // 柜号/车牌号
          ladingId: 'QWE456', // 提货号
          deviceId: 'PPPP', // 设备id
          deviceOnff: 1,
          deviceOnfftext: {EN: 'ON', CN: '开机'},
          startPosition: {
            country: { EN: '', CN: '' },
            city: { EN: '', CN: '' },
            province: {EN: '', CN: ''},
            address:{EN: '', CN: ''}
          }, // 起始地
          endPosition: {
            country: { EN: '', CN: '' },
            city: { EN: '', CN: '' },
            province: {EN: '', CN: ''},
            address: {EN: '', CN: ''}
          }, // 目的地
          startPositionEn: '', // 起始地英文
          transportStatus: 0, // 运输状态
          transportStatusCn: '',
          transportStatusEn: '',
          transportType: {EN: 'ON', CN: '运输中'}, // 运输类型
          deviceStatus: 0, // 设备状态
          time: '09:30', // 创建时间
          year: '2017', // 年
          month: '9', // 月
          data: '26', // 日
          dateText: '', // 今天/昨天
          transportTypeText: {},
          transportTypeTextEn: 'tian',
          icon: '',
          now: '南山区', // 当前位置
          ymd: '2017-09 09:30',
          locateFrequency: '',
          frequencyTypeText: {EN: '', CN: ''}
        },
        loading: false,
        ladingIdBillShow: false,
        dialogVisible: false,
        fromData: {
          id: null,
          recyclePosition:{country: null, city: null, address: null},
          transportStatus: 1,
          deviceNumber: null
        },
      }
    },
    methods: {
      ...mapActions({
        transportListpost: 'transportListFn',
        updateTansportStatuspost: 'updateTansportStatus',
        delPost: 'deletefn',
        checkID: 'checkId',
        mapListFn: 'mapListFn',
        chart: 'detail',
        updateBillon: 'updateBillon'
      }),
      saechList (id) {
        var parm = {
          id: id
        }
        this.chart(parm)
      },
      // 查询按钮
      saechFn () {
//        this.loading = true;
//        if(this.input){
//          var parm = {
//            code: this.input
//          }
//          this.checkID(parm)
//        } else {
//          var parm = {id: 1}
////          this.transportListpost(parm)
//        }
      },
      // 列表解析
      listFn (res) {
        var res = res.body.data
        // 重置列表
//        this.listData = {}
        // 提单号 ，运输状态描述，时间解析，今天/明天
        var ladingId, transportTypeText, time, dateText,completeTime
        // 创建时间
        if (res.createTime) {
          time = Public.publicLo.timeCheng(res.createTime)
        } else {
          time = {ymd: null, hms: null, times: null, s: null, d: null, m: null, md: null, week: null, y: null}
        }
        // 完成时间
        if (res.completeTime) {
          completeTime = Public.publicLo.timeCheng(res.completeTime)
        } else {
          completeTime = {ymd: null, hms: null, times: null, s: null, d: null, m: null, md: null, week: null, y: null}
        }
        // 当前时间
        var now = Public.publicLo.dataChange()
        // 匹配当前日期 和当前日期一样 赋值今天
        if ((now.y + now.m) == (time.y + time.m)) { // 匹配当前 年月
          var date1 = parseInt(now.d),
            date2 = parseInt(time.d)
          if (date1 == date2) { // 匹配当前日期
            dateText = '今天'
          } else if ((date1 - date2) == 1) {
            dateText = '昨天'
          }
        }
        // 拼接字符串
        transportTypeText = this.ransportSwitch(res.transportType)
        // 起运地解析地址
        var strdata = this.ctiyChart(res.startPosition)
        if (res.startPosition.address) {
          strdata.address.CN += res.startPosition.address
          strdata.address.EN += res.startPosition.address
        }
        // 目的地解析地址
        var endata = this.ctiyChart(res.endPosition)
        if (res.endPosition.address) {
          endata.address.CN += res.endPosition.address
          endata.address.EN += res.endPosition.address
        }
        var deviceOnfftext = {
          CN: '',
          EN: ''
        }
        //  默认状态
        deviceOnfftext = {CN: '异常', EN: 'error'}
        if (res.deviceOnff && res.deviceOnff==1){
          deviceOnfftext = {CN: '开', EN: 'ON'}
        } else {
          if (res.deviceOnff == 0) {
            deviceOnfftext = {CN: '关', EN: 'OFF'}
          }
        }
        // 频率文本
        var frequencyTypeText = this.frequencyTypeFn(res.frequencyType)
        if (res.newAddress) {
          if (res.newAddress.length > 10) {
//            res.newAddress = res.newAddress.substring(0,10) + '....'
          }
        } else {
          res.newAddress = '未请求到数据'
        }
        var list = {
          id: this.id,  // 后台唯一id
          containerId: res.containerId, // 柜号/车牌号
          ladingId: res.ladingId, // 提货号
          deviceId: res.deviceId, // 设备id
          deviceOnff: res.deviceOnff,
          deviceOnfftext: deviceOnfftext,
          startPosition: strdata, // 起始地
          endPosition: endata, // 目的地
          transportStatus: res.transportStatus, // 运输状态
          transportStatusCn: res.transportStatus == 1 ? '运输完成' : res.transportStatus == 0 ? '运输中' : '异常',
          transportStatusEn: res.transportStatus == 1 ? 'transport completion' : res.transportStatus == 0 ? 'transport on' : 'error',
          transportType: res.transportType, // 运输类型
          deviceStatus: 0, // 设备状态
          time: time.hms, // 创建时间
          year: parseInt(time.y), // 年
          month: time.m, // 月
          data: time.d, // 日
          dateText: dateText, // 今天/昨天
          transportTypeText: transportTypeText.type,
          transportTypeTextEn: transportTypeText.typeEn,
          icon: transportTypeText.icon,
          now: res.newAddress, // 当前位置
          ymd: time.ymd + ' ' + time.hms,
          completeTime: completeTime.ymd + ' ' + completeTime.hms,
          locateFrequency: res.locateFrequency,
          frequencyTypeText: frequencyTypeText
        }
        this.listData = list
      },
      // 解析运输方式
      ransportSwitch (data) {
        var transportTypeText
        switch (data) {
          case 0:
            transportTypeText = {type: '火车', icon: IconTrainNor, typeEn: 'Train'}
            break
          case 1:
            transportTypeText = {type: '汽车', icon: IconCarNor, typeEn: 'Motor'}
            break
          case 2:
            transportTypeText = {type: '轮船', icon: IconShipNor, typeEn: 'Freighter'}
            break
          case 3:
            transportTypeText = {type: '飞机', icon: IconPlantNor, typeEn: 'Flight'}
            break
          default:
            transportTypeText = { type: '异常', icon: IconCarNor }
            break
        }
        return transportTypeText
      },
      // 解析地址
      ctiyChart (data) {
        var str
        // 选择国家
        var country = jsonListData[data.country] || {EN: '', CN: ''}
        if (data.city) {
          str = data.city.split('_')
        }else {
          return {
            country: { EN: country.EN, CN: country.CN },
            city: { EN: '', CN: '' },
            province: { EN: '', CN: '' },
            address: {
              EN: country.EN + ' ',
              CN: country.CN + ' '
            }
          }
        }
        var  province = country[data.country +  '_' + str[1]],
          city = province[str[0] + '_' + str[1] +  '_' + str[2]]
        var address = {
          country: { EN: country.EN, CN: country.CN },
          city: { EN: city.EN, CN: city.CN },
          province: { EN: province.EN, CN: province.CN },
          address: {
            EN: country.EN + ' ' + province.EN + ' ' + city.EN + ' ',
            CN: country.CN + ' ' + province.CN + ' ' + city.CN + ' '
          }
        }
        return address
      },
      // 解析频率
      frequencyTypeFn (str) {
        var data = {EN: '', CN: ''}
        if (str) {
           data.EN = str
           str == 'min' ? data.CN = '分钟' : str == 'hour' ? data.CN = '小时' : str == 'day' ? data.CN = '天' : ''
        }
        return data
      },
      // 运输完成
      updatStatusFn (id) {
        var parm = {
          id: id,
          transportStatus: '1' // 0：运输中 1：运输完成 2：运输异常
        }
        this.updateTansportStatuspost(parm)
      },
      // 删除运输列表
      del (id) {
        var parm = {
          id: JSON.stringify(id)
        }
        this.delPost(parm)
      },
      // 运输列表操作
      listStatus (res) {
        if (res.status == 0) {
          // 修改成功
          this.openSuccess(res.message)
          if (this.input) {
           // this.saechFn()
          } else {

          }
        }
        if (res.status != 0) {
          // 修改成功 修改失败
          this.openError(res.message)
        }
        // 清空 vuex 中的本次记录，以免被下次操作状态造成混淆
        this.$store.commit('updateStatusTo', {body: null})
      },
      // 跳转地图
      gomap (list) {
        // 清空 vuex 列表
        this.$store.commit('addMapList', null)
        this.$store.commit('shechDat', list.deviceId)
        this.$router.push({name: 'map', query: {id: list.id}})
        return false
      },
      goBack () {
        this.$router.go(-1)
      },
      // 查看详情
      goChart (list) {
//        this.$router.push({name: 'chartSon', query: {id: list.id}})
      },
      // 点击修改提单号
      updateBill (id) {
        this.ladingIdBillShow = !this.ladingIdBillShow
        if (id) {
          var str = this.listData.ladingId
          var parm = {id: id, ladingId: this.listData.ladingId}
          this.updateBillon(parm)
        } else {
        return
        }
      },
      openSuccess (text) {
        this.$message({
          message: text,
          type: 'success'
        })
      },
      openError (text) {
        this.$message.error(text)
      },
      copyFn (event) {
        const clipboard = new Clipboard('.copy');
        var text = '复制成功'
        this.openSuccess(text)
      },
    },
    computed: {
      updateStatusData: function () {
        var parm = this.updateStatus
        if (parm) {
          this.listStatus(parm)
        }
        return 1
      },
      DetailData: function () {
        var parm = this.Detail
        if (parm) {
          this.listFn(parm)
        }
        return 1
      },
      languageData: function () {
        var parm = this.language
        if (parm) {
          if (parm.check == 'Chinese') {
            parm = parm.Chinese
          }
          if (parm.check == 'English') {
            parm = parm.English
//            this.formConfig.labelWidth = '200px'
//            dom.style = 'width:620px'
          }
          // 替换 其他交互数据
//          this.updateDate(parm)
        }
        return parm
      },
      ...mapState([
        'updateStatus',
        'language',
        'Detail'
      ])
    },
    mounted () {
      // 配置顶部菜单
      var parm = {logo: true, login: false, us: true, language: false, user: true, nav: true}
      this.$store.commit('updateNave', parm)
     var id = this.$route.query.id
      if (id) {
        this.saechList(id)
        this.id = id
      }
    },
    components: {
      ElButton,
      HeaderPublc
    }
  }
</script>




